.v-file {
  position: relative;
}

.v-file-input {
  position: relative;
  z-index: 2;
  width: 100%;
  height: calc(1.5em + 0.75rem + 2px);
  margin: 0;
  display: block;
  opacity: 0;
  cursor: pointer;
}

.v-file-label {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  border: 1px solid #ced4da;
  line-height: 1.5;
  font-size: 1rem;
  padding: 0.375rem 0.75rem;
  font-weight: 400;
  border-radius: 0.25rem;
  left: 0;
  top: 0;
  cursor: pointer;
  background: #FFF;
}

.v-file-output {
  display: block;
  flex-grow: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.v-file-button {
  display: inline-block;
  background: #DDD;
  font-size: 0.875rem;
  padding: 0 10px;
  border-radius: 0.25rem;
}

.label {
  display: inline-block;
  margin-bottom: 0.5rem;
}
.line-clamp-2 {
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
}

.v-select-output {
  text-align: left;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.v-select-output:before {
  content: "";
  border-left: 0.5em solid transparent;
  border-right: 0.5em solid transparent;
  border-top: 0.5em solid currentColor;
  display: block;
  float: right;
  margin-left: 0.75rem;
  margin-top: 0.5rem;
}

.v-options {
  width: 100%;
  min-width: 180px;
  padding-top: 0;
  padding-bottom: 0;
  max-height: 220px;
  overflow: auto;
  max-height: 300px;
  overflow: auto;
  padding-left: 0;
  list-style: none;
  margin-bottom: 0;
}

.v-select-filter-area {
  padding-left: 10px;
  padding-right: 10px;
}

.v-select-filter {
  width: calc(100% - 20px);
  margin-left: auto;
  margin-right: auto;
  padding: 0.375rem 0.75rem;
  border: 1px solid #ccc;
  border-radius: 0.25rem;
  line-height: 1.5;
  font-size: 1rem;
  height: calc(2.25rem + 2px);
  margin-bottom: 15px;
  display: block;
  outline: none;
}
.v-select-filter:focus {
  border-color: #007bff;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.2);
}
.v-option {
  padding: 10px;
  color: #232323;
  cursor: pointer;
}
.v-option:not(.selected):hover {
  background: #eee;
}
.v-option.selected {
  background: var(--primary);
  color: var(--primary-inverse, #FFF);
}
.v-option.disabled {
  color: #999;
  pointer-events: none;
}
