.msg__cookies {
  background: #FFF;
  position: fixed;
  bottom: 15px;
  left: 50%;
  transform: translateX(-50%);
  padding: 15px;
  border-radius: 15px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.8);
  width: 100%;
  z-index: 999999999;
  border: 1px solid rgba(0, 0, 0, 0.2);
  font-family: "Abhaya Libre", serif;
  font-size: 20px;
}
.msg__cookies a {
  color: #ecb7af;
  text-decoration: underline;
}
.msg__cookies .btn {
  background: #ecb7af;
  color: #FFF;
  font-weight: 800;
  font-family: "Abhaya Libre", serif;
  box-shadow: none;
  transition: background 0.4s linear;
}
.msg__cookies .btn:hover {
  background: #dd7f71;
}
@media (max-width: 767.98px) {
  .msg__cookies {
    max-width: calc(100% - 30px);
    font-size: 12px;
    text-align: center;
  }
  .msg__cookies a {
    display: inline-block;
  }
  .msg__cookies .content {
    margin-bottom: 10px;
  }
}
@media (min-width: 768px) {
  .msg__cookies {
    display: grid;
    grid-template-columns: 1fr min-content;
    grid-column-gap: 10px;
    align-items: center;
  }
  .msg__cookies .btn {
    white-space: nowrap;
  }
}
@media (min-width: 768px) and (max-width: 991.98px) {
  .msg__cookies {
    max-width: 720px;
  }
}
@media (min-width: 992px) and (max-width: 1199.98px) {
  .msg__cookies {
    max-width: 960px;
  }
}
@media (min-width: 1200px) {
  .msg__cookies {
    max-width: 1140px;
  }
